<template>
  <div class="collection">
    <van-nav-bar v-model="active" title="我的收藏" left-text="返回" left-arrow @click-left="back" />
    <div v-if="allLikeData.length>0">
    <div class="index-welcome">
      <div class="index-recommend">
        <div class="recommend-content">
          <van-grid :column-num="2" :gutter="10" :border="false">
            <van-grid-item class="one-text" v-for="(item,index) in allLikeData" :key="index">
              <div class="pro-img" @click="goDetail(item.pid)">
                <img class="auto-img" :src="item.smallImg" alt />
                <div class="like">
                </div>
              </div>
              <div class="one-text">{{item.name}} <span class="spec">{{item.typeDesc}}</span></div>
              <div class="pro-price">
                ￥{{item.price}}
                <span class="delete-icon fr" @click="deleteLike(item.pid, index)">
                  <van-icon name="delete" />
                </span>
              </div>
            </van-grid-item>
          </van-grid>
        </div>
      </div>
    </div>
    </div>
    <div v-else>
      <van-empty description="您还没有收藏任何商品">
        <van-button round type="default" class="bottom-button" @click="goMenu">前往收藏</van-button>
      </van-empty>
    </div>
  </div>
</template>

<script>
import "../assets/less/collection.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      active: 0,
      allLikeData: [],
    };
  },
  created() {
    this.findAllLike();
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    goMenu() {
      this.$router.push({ name: "Menu" });
    },
    //查找所有收藏商品
    findAllLike() {
      let tokenString = localStorage.getItem("WTPTK");
      if (!tokenString) {
        this.$notify({
          message: "请先登录",
          color: "#fff",
          background: "#3585f9",
          duration: 800,
        });
        this.$router.push({ name: "Login" });
      }
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAllLike",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
          status,
        },
      })
        .then((result) => {
          this.orderData = [];
          this.$toast.clear();
     
          if (result.data.code == 2000) {
            this.allLikeData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    goDetail(pid) {
      this.$router.push({ name: "Detail", query: { pid } });
    },
    //删除收藏商品
    deleteLike(pid, index) {
      this.$dialog
        .confirm({
          title: "删除收藏商品",
          message: "是否删除收藏商品",
          confirmButtonColor: "#3585f9",
        })
        .then(() => {
          // on confirm
          let tokenString = localStorage.getItem("WTPTK");
          if (!tokenString) {
            this.$notify({
              message: "请先登录",
              color: "#fff",
              background: "#3585f9",
              duration: 800,
            });
            this.$router.push({ name: "Login" });
          }
          this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
            loadingType: "spinner",
          });
          let data = {
            appkey: this.appkey,
            pid: pid,
            tokenString: localStorage.getItem("WTPTK"),
          };
          data = utils.queryString(data);
          this.axios({
            method: "POST",
            url: "/notlike",
            data,
          })
            .then((result) => {
              this.$toast.clear();
        
              if (result.data.code == 900) {
                this.allLikeData.splice(index, 1);
                this.$toast("删除收藏商品成功");
              } else {
                this.$toast("删除收藏商品失败");
              }
            })
            .catch((err) => {
              this.$toast.clear();
              
            });
        })
        .catch(() => {
          // on cancel
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>